import React, { Component } from 'react';
import './App.css';
import CalendarHeader from './components/CalendarHeader';
import CalendarBody from './components/CalendarBody';
import Events from './components/Events';
import moment from 'moment';

const events = [
  {
    date: '17-02-2019',
    events:[
      {status: 'pending', name:'event name2', body: 'event bod2', time: '12:00'},
      {status: 'pending', name:'event name', body: 'event body', time: '11:00'}
    ]
  },
  {
    date: '20-02-2019',
    events:[
      {status: 'pending', name:'event 3', body: 'event 3', time: '12:00'}
    ]
  },
  {
    date: '27-02-2019',
    events:[
      {status: 'pending', name:'event 3', body: 'event 3', time: '12:00'}
    ]
  },
  {
    date: '28-03-2019',
    events:[
      {status: 'pending', name:'other month', body: 'other month', time: '00:00'}
    ]
  }
];

export class Calendar extends Component {
  constructor(props){
    super(props);
    this.state = {
      mode: 'month',
      currentContext: moment(),
      selectedDate  : moment(),
      currentDate   : moment(),
      selectedEvent : null,
      events        : events
    }
  }
  
  setSelectedEvent = (data) =>{
    this.setState({
      selectedEvent: data
    }); 
  }

  setEvents = (data) => {
    this.setState({
      events: data
    }); 
  }

  setMode = (data) => {
    this.setState(
      {mode: data
    });
  }

  setSelectedDate = day => {
    this.setState({
      selectedDate: day
    });
  }

  setCurrentContext = (data) => {
    this.setState({
      currentContext: data
    });
  }

  render() {
    const {mode, selectedDate, selectedEvent, events, currentDate} = this.state;
    return (
      <div id="calendar">      
        <CalendarHeader selectedDate = {selectedDate}
                        mode = {mode}
                        events = {events}
                        selectedEvent = {selectedEvent}
                        setCurrentContext = {this.setCurrentContext}
                        setSelectedDate = {this.setSelectedDate}
                        setSelectedEvent = {this.setSelectedEvent}
                        setEvents = {this.setEvents}
                        setMode = {this.setMode}
                        />
        <CalendarBody mode = {mode}
                      selectedDate = {selectedDate}
                      currentDate = {currentDate}
                      setSelectedDate = {this.setSelectedDate}
                      events = {events}
                      />
        <Events events={events}
                selectedDate = {selectedDate}
                mode = {mode}
                currentDate = {currentDate}
                setEvents = {this.setEvents}
                setSelectedEvent = {this.setSelectedEvent}
                />
      </div>
    )
  }
}

export default Calendar